<template>
    <div class="PcbValuation">
      <PcbTop  :PcbTopTitle="PcbTitle"></PcbTop>
      <div class="Pcbhint">
        温馨提示：付款时间（非下单时间）在20点前交期从当天开始计算，20点后付款、20点30分后更新文件或21点确认工程问题，交期将增加1天；周六20点后和周日下单付款等同于周一下单付款。
      </div>


      <div class="orderForm" v-for="(data,index) in OrderContent">
        <div class="title" @click="PcbParticulars">
          <p @click.stop="SelectHe(data,index)"><i class="icon_show" :class="data.select==true?'bg_icon':'bg_icons'"></i><span>FR-4生益</span></p>
          <p> <i class="iconfont">&#xe67c;</i></p>
        </div>
        <div class="bottom">
          <p><span>单片价格 ：</span><span>{{data.jiage}}</span></p>
          <p><span>数量/面积  ：</span><span>{{data.mianji}}</span></p>
          <p><span>交期：</span><span>{{data.jiaoqi}}</span></p>
          <p><span>总价格 ：</span><span class="cl_f90">￥{{data.zongjiage}}</span></p>
        </div>
      </div>
      <div class="FillOver both">
        <p>合计 <span class="cl_f90">${{totalPrice}}</span></p>
        <span class="nextStep" @click="nextStep">加入购物车</span>
      </div>
    </div>

</template>

<script>
    import Vue from "vue"
    import PcbTop from "../../common/PcbTop";

    export default {
        name: "PcbValuation",
        components: {PcbTop},
        data() {
            return {
              //像子组件传值
              PcbTitle:{name:"PCB在线计价",class:true},
              select:true,
              OrderContent:[
                {name:"FR-4生益",jiage:"72.20/pcs",mianji:"5 pcs/ 0.2000㎡",jiaoqi:"48小时",zongjiage:"100.00",select:true},
                {name:"FR-4生益",jiage:"72.20/pcs",mianji:"5 pcs/ 0.2000㎡",jiaoqi:"48小时",zongjiage:"100.00",select:true},
                {name:"FR-4生益",jiage:"72.20/pcs",mianji:"5 pcs/ 0.2000㎡",jiaoqi:"48小时",zongjiage:"100.00",select:true},
                {name:"FR-4生益",jiage:"72.20/pcs",mianji:"5 pcs/ 0.2000㎡",jiaoqi:"48小时",zongjiage:"100.00",select:true},
                {name:"FR-4生益",jiage:"72.20/pcs",mianji:"5 pcs/ 0.2000㎡",jiaoqi:"48小时",zongjiage:"100.00",select:true},
              ],
              //总价格
              totalPrice:"0.00",
            }
        },
        methods: {
          //跳转添加购物车
          nextStep(){
            this.$router.push({
              path:"PcbSucceed"
            })
          },
          returnG(){this.$router.go(-1)},
          messages(){this.$router.push({path: "SettingContent",})},
          SelectHe(data,index){
            data.select=!data.select;

            if(data.select==false){
              this.totalPrice=Number(this.totalPrice)+Number(data.zongjiage);
              console.log(this.totalPrice);
            }else{
              this.totalPrice=Number(this.totalPrice)-Number(data.zongjiage);
              console.log(this.totalPrice);
            }
          },
          //跳转
          PcbParticulars(){
            this.$router.push({
              path:"PcbParticulars"
            })
          },
        },
        created() {

        }
    }
</script>
<style scoped>
  .PcbValuation .orderForm{margin-top: .24rem}
  .PcbValuation .orderForm .title{height: 1.02rem;line-height: 1.02rem;width: 100%;background: #fff;display: flex;justify-content: space-between;padding: 0 .3rem;border-bottom: 1px solid #E3E4E8;}
  .PcbValuation .orderForm .title p .icon_show{width: .4rem;height: .4rem;display: inline-block;vertical-align: middle;margin-right: .24rem}
  .PcbValuation .orderForm .title  p span{display: inline-block;vertical-align: middle;font-size: .34rem;color: #333}
  .PcbValuation .bg_icon{background: url("../../../assets/img/common/Nopitch.png") no-repeat;background-size: cover;}
  .PcbValuation .bg_icons{background: url("../../../assets/img/common/pitch.png") no-repeat; background-size: cover;}
  .PcbValuation .orderForm .bottom{padding: 0.15rem .3rem;background: #fff}
  .PcbValuation .orderForm .bottom p{display: flex;justify-content: space-between;line-height: .6rem;font-size: .28rem;}
  .PcbValuation .orderForm .bottom p span:first-child{color: #666}
  .PcbValuation .orderForm .bottom p span:last-child{color: #333}


  .PcbValuation .HomePageTop .Head{background: #fff}
  .PcbValuation .iconfontRight{ display: inline-block;  position: absolute;right: .24rem;}
  .PcbValuation .iconfontRight i{color: #f90;}


  .PcbValuation .FillOver{position: fixed;bottom: 0;width: 100%;height: 1.12rem;line-height: 1.12rem;box-shadow: 0px -3px 5px 0px
  rgba(0, 0, 0, 0.12);}
  .PcbValuation .FillOver p{display: inline-block;float: left;width: 50%;text-align: left;font-size: .34rem;background: #fff;color: #666;height: 100%;padding-left: .3rem}
  .PcbValuation .FillOver p span{background: #fff;}
  .PcbValuation .FillOver .nextStep{display: inline-block;float: left;width: 50%;background: #f90;color: #fff;font-size: .34rem;text-align: center;height: 100%}
</style>



